body

{  background: -webkit-linear-gradient(
    350deg
    , #46b587, #949494);  background: -o-linear-gradient(
    350deg
    , #46b587, #949494);  background: linear-gradient(
    100deg
    , #46b587, #949494);
        background-repeat: repeat;
        background-size: cover;
}
.App-logo {
/*  animation: App-logo-spin infinite 20s linear;*/
  height: 60px;
  float: left;;
}

.App-header {
  background:-webkit-gradient(linear, left top, right top, from(#059B9A), to(#53BA83));
  background:-webkit-linear-gradient(left, #059B9A, #53BA83);
  background:-o-linear-gradient(left, #059B9A, #53BA83);
  background:linear-gradient(to right, #059B9A, #53BA83);
  height: 100px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  float: left;
  padding-left:15px
}
.App-title-right
{
   float: right;
   font-size: 1.5em;
  }

.App-logo-right {
   -webkit-animation: App-logo-spin infinite 20s linear;
           animation: App-logo-spin infinite 20s linear;
    height: 60px;
    float: right;
  }
.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.navbar {
  border-radius: 0px;
}


.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color:#3b3958;
  color: white;
  text-align: center;
  padding: 5px;
}
.navbar-inverse .navbar-nav>li>a {
  color: #333;
}
.navbar-inverse {
  background-color: #fbfff2;
  border-color: #080808;
}
.navbar-inverse .navbar-nav>li>a:focus, .navbar-inverse .navbar-nav>li>a:hover {
  color: #f9f9f9;
  background-color: #333;
  -webkit-transition: 0.3s all;
  -o-transition: 0.3s all;
  transition: 0.3s all;
 
}


